@import url(https://fonts.googleapis.com/css?family=Saira+Semi+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  max-height:100%;
  width: 100%;
  margin: 0;
}

#root {
  width: 100%;
  max-height:100%;
  height: 100%;
  background-color: white;
  position: absolute;
  padding:3%;
}
 
.mark{background-color:red;}
.red{color:red;}
.pause{ -webkit-animation-play-state: paused !important; 
  animation-play-state: paused !important;}

button{min-width:60px;border:1px solid #efefef;text-align: left;padding:5px;}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap:wrap;
  padding-bottom: 10px;
} 

ul li {
  padding:5px;
} 
ul li a{
 color:#333333;text-decoration: none;
} 
.active{
 color:#333333;text-decoration: underline;
} 

